
.actionBtn {
  font-size: 15px;
  font-weight: 500;
  color: #002d36;
  background-color: #99c6cf;
  border-radius: 6px;
  padding: 10px 25px;
  border: none;
  cursor: pointer;
  transition: all 0.5s ease;
  
  

}
.container {
    width: 700px;
    display: flex;
    overflow-x: scroll;
    scroll-behavior: smooth;
  }

  .info {
    display: flex;
    
  }

  .title {
    text-align: center;
    color: #333;
  }
  
  .workoutCard {
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 15px;
    padding: 15px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .workoutCard h3 {
    margin-bottom: 10px;
    color: #007bff;
  }
  
  .workoutCard p {
    color: #555;
  }
  

  