.container {
    padding: 400px;
    padding-top: 60px; /* Отступ сверху 20px */
    display: flex;
    flex-direction: column; /* Устанавливаем направление флекс-контейнера на вертикальное */
    align-items: stretch; /* Растягиваем дочерние элементы на всю доступную ширину */
}

.planInfo {
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 20px; /* Добавляем отступ между блоками */
}

.trainersInfo {
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;

}

img {
    border-radius: 50%;
    max-width: 100%;
}

button {
    margin-top: 20px;
}
