.container {
    display: flex;
    margin-top: 20px;

    justify-content: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    
}


.childs {
    border: solid 1px;
    margin: 20px;
    border-radius: 15px;
    transition: transform 0.3s ease-in-out;

}
.childs:hover {
    transform: scale(1.05);
}

/* SubscriptionPage.module.css */

.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
  }
  
  .modalontent {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
  }
  
  .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
  


